/** @jsx jsx */
import { jsx, Styled } from "theme-ui";
import React, { useState, useEffect, useRef, createRef } from "react";
import { rem } from "@src/theme";
import SEO from "@src/components/SEO";
import Unions from "@src/components/unions";
import { countries } from "@src/data";
import footerImg from "@static/images/dingbangs.png";

import Union1 from "@src/components/unions/1";
import Union2 from "@src/components/unions/2";
import Union3 from "@src/components/unions/3";
import Union4 from "@src/components/unions/4";
import Union5 from "@src/components/unions/5";
import Union6 from "@src/components/unions/6";
import Union7 from "@src/components/unions/7";
import Union8 from "@src/components/unions/8";

const IndexPage = (props) => {
  const [showInfo, setShowInfo] = useState(false);
  const [country, setCountry] = useState(null);
  const [currentImage, setCurrentImage] = useState(null);
  const [topPos, setTopPos] = useState(0);
  const [leftPos, setLeftPos] = useState(0);

  const [sortOption, setSortOption] = useState(null);
  const [stampImage, setStampImage] = useState(null);

  const parentRefs = useRef(countries.map(() => createRef()));

  const onCountryHover = (c, index) => {
    setCountry(c);
    setTopPos(getRandomTop(parentRefs.current[index]));
    setLeftPos(getRandomLeft(parentRefs.current[index]));
  };
  const onCountryLeave = (c) => {
    setCountry(null);
    setCurrentImage(null);
  };
  const onSortSelect = (o) => setSortOption(o);

  useEffect(() => {
    if (country) {
      if (country.stamps.length === 1) {
        setCurrentImage(0);
        return;
      }
      if (currentImage === null) {
        setCurrentImage(0);
      }
      const next =
        currentImage === 0 ? 1 : (currentImage + 1) % country.stamps.length;
      const id = setInterval(() => setCurrentImage(next), 1500);
      return () => clearInterval(id);
    }
  }, [currentImage, country]);
  const getRandomTop = (parentRef) =>
    `${Math.floor(Math.random() * (parentRef.current.clientHeight - 24)) +
      1}px`;
  const getRandomLeft = (parentRef) =>
    `${Math.floor(Math.random() * parentRef.current.clientHeight) + 1}px`;

  return (
    <div
      sx={{
        width: ["95%"],
        maxWidth: [rem(1380), null, null, rem(1530)],
        mx: "auto",
        mt: [rem(30), null, rem(40)],
        pb: [rem(30), null, rem(40)],
      }}
    >
      <SEO defaultTitle="Home" location={props.location} />
      <div
        sx={{
          mb: rem(30),
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <div
          sx={{
            transform: "translateY(-20px)",
          }}
        >
          <h1
            sx={{
              fontFamily: "Authentic",
              fontSize: rem(76),
              mb: rem(0),
              mt: rem(16),
              lineHeight: 1,
              textTransform: "uppercase",
            }}
          >
            Across the seas
          </h1>
          <p
            sx={{
              mt: 0,
              fontSize: rem(24),
            }}
          >
            Hover over countries to view stamps
          </p>
        </div>

        <div
          sx={{
            display: "flex",
            alignItems: "flex-start",
          }}
        >
          {showInfo && (
            <p
              sx={{
                fontSize: rem(18),
                mt: 0,
                maxWidth: rem(448),
                pr: rem(10),
              }}
            >
              This stamps database has been collected as part of research for
              the ISTD project by Maria, exploring the intersection of personal
              and national migrant identities. Do passport stamps accurately
              reflect national identity?
            </p>
          )}
          <button
            onClick={() => {
              setShowInfo(!showInfo);
            }}
            sx={{
              fontSize: rem(24),
              transform: "translateY(-4px)",
              "&:hover": {
                color: "red",
              },
            }}
          >
            Info
          </button>
        </div>
      </div>
      <ul
        sx={{
          margin: 0,
          padding: 0,
          display: "flex",
          listStyle: "none",
          flexDirection: ["column", null, "row"],
          flexWrap: "wrap",
        }}
      >
        {countries.map((stamp, index) => (
          <li
            tabIndex={0}
            ref={parentRefs.current[index]}
            onMouseEnter={() => onCountryHover(stamp, index)}
            onMouseLeave={onCountryLeave}
            onFocus={() => onCountryHover(stamp, index)}
            onFocusLeave={onCountryLeave}
            sx={{
              position: "relative",
              mr: rem(22),
              mb: [0, null, rem(-8)],
              "&:hover, &:focus": {
                color: "red",
                outline: "none",
                cursor: "pointer",
              },
              transition: "color 200ms linear",
              fontSize: [rem(40), rem(45)],
              position: "relative",
              lineHeight: [1.2, null, 1.4],
            }}
          >
            {stamp.stamps.length > 1 && (
              <span
                sx={{
                  fontSize: rem(20),
                  transform: "translate(1px,-22px)",
                  display: "inline-block",
                }}
              >
                {stamp.stamps.length}
              </span>
            )}
            <span>{stamp.name.toUpperCase()}</span>

            <img
              src={country ? country.stamps[currentImage] : null}
              sx={{
                display:
                  country && country.name === stamp.name ? "block" : "none",
                position: "absolute",
                zIndex: 2,
                maxWidth: rem(190),
                top: topPos,
                left: leftPos,
              }}
            ></img>
          </li>
        ))}
      </ul>

      <div
        sx={{
          position: "fixed",
          bottom: 0,
          display: "flex",
          alignItems: "center",
          bottom: 0,
          width: ["95%"],
          maxWidth: [rem(1380), null, null, rem(1530)],
          mx: "auto",
          paddingBottom: "20px",
          div: {
            marginRight: "40px",
            height: "100%",
            width: "100%",
          },
        }}
      >
        <div>
          <Union5 />
        </div>

        <div>
          <Union2 />
        </div>

        <div>
          <Union6 />
        </div>

        <div>
          <Union1 />
        </div>

        <div>
          <Union4 />
        </div>

        <div>
          <Union8 />
        </div>
        <div>
          <Union7 />
        </div>
        <div>
          <Union3 />
        </div>
        <div>
          <Union5 />
        </div>

        <div>
          <Union2 />
        </div>

        <div>
          <Union6 />
        </div>

        <div>
          <Union1 />
        </div>

        <div>
          <Union4 />
        </div>

        <div>
          <Union8 />
        </div>
        <div>
          <Union7 />
        </div>
        <div>
          <Union3 />
        </div>
        <div>
          <Union5 />
        </div>

        <div>
          <Union2 />
        </div>

        <div>
          <Union6 />
        </div>
      </div>
    </div>
  );
};
export default IndexPage;
